* {
  box-sizing: border-box; }

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace; }

html, body, #root {
  height: 100vh; }

html {
  height: 100vh;
  background: #eaeaea; }

.bg-image {
  background: url("/vs-code.png");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  filter: blur(2px);
  -webkit-filter: blur(2px);
  height: 100%;
  width: 100%;
  position: fixed; }

#root {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center; }

.App {
  position: absolute;
  top: 2em;
  max-width: 800px;
  margin: auto;
  margin-bottom: 2em;
  background: #fff;
  box-shadow: rgba(0, 0, 0, 0.4) 0px 0px 5px;
  -webkit-transition: all 1s;
  transition: all 1s; }
  .App .tabs .ms-Pivot-text, .App .tabs .ms-Pivot-icon, .App .tabs .ms-Pivot-count {
    color: #fff; }
  .App .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
    height: 40vmin; }
  .App .App-header {
    background-color: #008272;
    padding-bottom: 0px;
    color: white; }
    .App .App-header .name {
      margin-bottom: 0px; }
    .App .App-header .title {
      display: block;
      margin-bottom: 20px;
      color: rgba(255, 255, 255, 0.7); }
  .App .tab-content {
    font-size: 1em;
    padding: 1.5em; }
    .App .tab-content *:first-child {
      margin-top: 0px; }
  .App .profile {
    padding: 20px 0px; }
    .App .profile .ms-Persona > .ms-Persona-details > .ms-Persona-primaryText, .App .profile .ms-Persona-primaryText:hover {
      color: #ffffff; }
    .App .profile .ms-Persona-secondaryText {
      color: rgba(255, 255, 255, 0.7); }
  .App .experience .position {
    margin-bottom: 1em; }
    .App .experience .position:not(:last-child) {
      border-bottom: 1px solid #eaeaea; }
    .App .experience .position .title {
      margin: 0; }
    .App .experience .position .description {
      font-weight: 100; }
      .App .experience .position .description {
        padding-top: 1em; }
    .App .experience .position .period {
      font-size: 0.8em;
      color: rgba(0, 0, 0, 0.6); }
    .App .experience .position .company {
      font-style: normal;
      color: rgba(0, 0, 0, 0.7); }
  .App .container {
    width: 100%;
    margin: auto;
    padding: 0px 20px; }

